import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/htmlNote',
    name: 'HTMLNote',
    component: () => import(/* webpackChunkName: "htmlNote" */ '../views/HTMLNote.vue')
  },
  {
    path: '/cssNote',
    name: 'CSSNote',
    component: () => import(/* webpackChunkName: "cssNote" */ '../views/CSSNote.vue')
  },
  {
    path: '/jsNote',
    name: 'JSNote',
    component: () => import(/* webpackChunkName: "jsNote" */ '../views/JSNote.vue')
  },
  {
    path: '/tsNote',
    name: 'TSNote',
    component: () => import(/* webpackChunkName: "tsNote" */ '../views/TSNote.vue')
  },
  {
    path: '/webpackNote',
    name: 'WebpackNote',
    component: () => import(/* webpackChunkName: "webpackNote" */ '../views/WebpackNote.vue')
  },
  {
    path: '/gitNote',
    name: 'GitNote',
    component: () => import(/* webpackChunkName: "gitNote" */ '../views/GitNote.vue')
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/usage',
    name: 'Usage',
    component: () => import(/* webpackChunkName: "usage" */ '../views/Usage.vue')
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior () {
    document.getElementById("content__body").scrollTo(0, 0);
  }
})

export default router
