<template>
  <div :class="{
      'sidebar': true,
      'sidebar--hide': hide,
      'sidebar--show': !hide,
    }">
    <div class='sidebar__title'>
      <span class='sidebar__title__bold'>Ming's Storage</span>
      <span class='sidebar__title__thin'>程序記憶</span>
    </div>
    <div class="sidebar__body">
      <div class="sidebar__body__home">
        <router-link to='/'>
          <div class="sidebar__body__link" @click="displayTabContent">Home</div>
        </router-link>
      </div>
      <div class="sidebar__body__else">
        <template v-for="(item) in sidebarList" :key="item.text">
          <router-link :to='item.to'>
            <div class="sidebar__body__link" @click="displayTabContent">{{item.text}}</div>
          </router-link>
        </template>
      </div>
      <div class="sidebar__body__website">
        <div class="sidebar__body__website__title">Related links</div>
        <div class="sidebar__body__website__body">
          <a class="sidebar__body__website__link" href="https://github.com/partyyaya" target="_blank">
            <div>
              <img src="../assets/github.png" alt="Github" width="15" height="15">&nbsp;
              Github
            </div>
          </a>
          <div class="sidebar__body__website__link"></div>
          <div class="sidebar__body__website__link"></div>
        </div>
      </div>
      <router-link to='About'>
        <div class="sidebar__body__about sidebar__body__link" @click="displayTabContent">About me</div>
      </router-link>
      <router-link to='Usage'>
        <div class="sidebar__body__pagetech sidebar__body__link" @click="displayTabContent">Page technology usage</div>
      </router-link>
      <div class="sidebar__body__copyright">
        © MingMing 2021. <br><br>All rights reserved.
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'

const hide = ref(true)
const sidebarList = [
  { text: 'HTML Note', to: { name: 'HTMLNote' } },
  { text: 'CSS Note', to: { name: 'CSSNote' } },
  { text: 'JS Note', to: { name: 'JSNote' } },
  { text: 'TS Note', to: { name: 'TSNote' } },
  { text: 'Git Note', to: { name: 'GitNote' } },
  { text: 'Webpack Note', to: { name: 'WebpackNote' } }
]

const switchSidebar = (status) => {
  hide.value = status
}

export default {
  name: 'Sidebar',
  props: ['currentIndex'],
  setup (props, context) {
    const displayTabContent = () => {
      hide.value = true
      context.emit('displayTabContent', true)
    }
    return { hide, sidebarList, switchSidebar, displayTabContent }
  }
}
</script>
<style lang='scss' scoped>
@import '../style/sidebar.scss';
</style>
