import axios from 'axios'
import { Base64 } from './third/base64'

const testpk = 'pFX12zNW28o7Fg9n4jKHy8zENNeP9Nghp_Vrz2b9'
const sequence = '4122331'
const getToken = () => {
  let combination = '';
  for(let i = 0; i < sequence.length; i++){
    if((i+1) % 2 == 0){
      continue
    } else {
      switch (sequence.substr(i, 1)) {
        case '1':
          combination += testpk.substr(0, 10);
          break;
        case '2':
          combination += testpk.substr(10, 10);
          break;
        case '3':
          combination += testpk.substr(20, 10);
          break;
        case '4':
          combination += testpk.substr(30, 10);
          break;
      }
    }
  }
  return combination
}

const instance = axios.create({
  baseURL: 'https://api.github.com/',
  timeout: 5000
})

const get = async (url, params = {}) => {
  const result = await instance.get(url, {
     params,
     headers: { Authorization: `token ${getToken()}` } 
  })
  return result.data
}

const post = async (url, data = {}) => {
  const result = await instance.post(url, data, {
    headers: {
      'Authorization': `token ${getToken()}`,
      'Content-Type': 'application/json'
    }
  }).catch(error => {
    console.log(error)
  })
  return result.data
}

const getMarkdownContent = async (url) => {
  let { content } = await get(url)
  content = Base64.decode(content)
  const data = { 
    "text": content,
    "mode": "markdown",
  }
  console.log(data)
  const result = await post('markdown', data)
  return result
}

export { get, post, getMarkdownContent }
